/* global jQuery */
/*
 * Wrap Marketo form fields to support two column layout
 */
(function ($) {

	"use strict";

	$(document).on("alterFormValuesComplete", function () {
		$(".mktoFormRow.FirstName, \
			.mktoFormRow.LastName, \
			.mktoFormRow.Title, \
			.mktoFormRow.relationshipCeleb, \
			.mktoFormRow.Email, \
			.mktoFormRow.numberofGuests, \
			.mktoFormRow.nameofGuests").wrapAll('<div class="fieldsWrapper" />');
	});

}(jQuery));